.App {
  height: 100%;

  .header-container {
    width: 100%;
    height: 80px;
  }

  .contents-container {
    display: flex;
    height: calc(100% - 160px);

    .side-contents-container {
      width: 250px;
      height: 100%;
    }

    .body-contents-container {
      width: calc(100% - 250px);
      height: 100%;
    }
  }

  .footer-container { 
    width: 100%;
    height: 80px;
  }

  .border {
    border: 1px solid black;
  }

  .border-top {
    border-top: 1px solid black ;
  }

  .border-bottom {
    border-bottom: 1px solid black;
  }

  .border-right {
    border-right: 1px solid black;
  }
}

.title {
  font-size: 2em;
  font-weight: bold;
}